@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

 
body {
  font-weight: 300;
  font-family: 'Raleway', sans-serif;
  background-color: rgb(241, 233, 207);
}

h1, h2, h3, h4, strong {
  font-weight: 400;
}

h1 {
  margin-bottom: 30px;
}

a, a:hover {
  color: inherit;
  text-decoration: none;
}



.toast {
  position: fixed;
  bottom: 10px;
  right: 10px;
  z-index: 99999;
  width: 350px
}

.error-message {
  background: tomato;
  color: white;
  padding: 10px
}

.homeButton {
    padding: 15px 35px;
    font-weight: 600;
    font-size: 1.3em;
    border: 2.5px solid #4b98b1;
    color:#4b98b1;
    transition: 0.3s
}

.loginButton {
    padding: 10px 30px;
    font-weight: 600;
    font-size: 1.3em;
    border: 2.5px solid #E8DAB2;
    color:#E8DAB2;
    transition: 0.3s
}

.loginButton:hover {
  color: rgb(30, 42, 48); 
  text-decoration: none;
  border-color: rgb(30, 42, 48);
}

.homeButton:hover {
  color: #E8DAB2;
  background-color: #4b98b1;
  text-decoration: none;
  border-color: rgb(241, 233, 207);
}

.homePhrase {      
    background-color: rgb(241, 233, 207);
}

.colPhrase {
    padding: 8% 2% 12% 12%; 
}

h1 {
    color: black;
    font-size: 4.5em;
    font-weight: bold;
    margin-bottom: 15%;
}

.bluePhrase {
    padding: 10% 12% 12% 10%;
    color: #E8DAB2;
}

.blueRow {   
    background-color: #4b98b1;
}

h2 {
    font-weight: 600;
    font-size: 2.5em;
    margin-bottom: 15%
}

.travolta {
    background-image: url(/static/media/travol.f2cfffde.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position-y: bottom;
    background-position-x: left;
}

.miaWallace {
    background-image: url(/static/media/miawallace.cbfd318d.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position-y: bottom;
    background-position-x: right;
}

.homeFooter {
    background-color: rgb(24, 49, 61);
    padding: .5%;
    color: #E8DAB2;
    display: flex;
    justify-content: center;
}

.homeFooter p {
    margin-top: 1%;
    text-align: center;
    font-weight: 600;
}
.nav {
    background-color: #d75a0e;
    color: #E8DAB2;
    box-shadow: 1px -9px 8px 9px rgba(0,0,0,0.6);
}

.nav a {
    color: #E8DAB2;
    font-weight: 600;
    transition: 0.3s;
}

.nav a:hover {
    color: #EAEAEA
}
